  display: block;
  position: absolute;
  left: 0;
  top: 0;
  transform-origin: 0 0;
  border: 1PX solid #ebebeb;
  border-radius: 2PX;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
}
