.cube-tab-bar
  position: relative
  display: flex
  align-items: center
  justify-content: center

.cube-tab-bar_inline
  .cube-tab
    display: flex
    align-content: center
    justify-content: center

.cube-tab-bar-slider
  position: absolute
  left: 0
  bottom: 0
  height: 2px
  width: 20px
  background-color: $tab-slider-bgc
