





















































































































































































































.cube-scroll-nav
  position: relative
  height: 100%
  overflow: hidden
  .cube-sticky-content
    height: 100%
.cube-scroll-nav-main
  overflow: hidden
  > .cube-sticky
    position: static
.cube-scroll-nav_side
  > .cube-sticky
    display: flex
    > .cube-scroll-wrapper
      flex: 1
    > .cube-sticky-fixed
      position: relative
      height: 100%
      order: -1
  .cube-scroll-nav-main
    > .cube-sticky-ele
      float: left
    > .cube-sticky
      overflow: hidden
