.cube-scroll-wrapper {
  position: relative;
  height: 100%;
  overflow: hidden;
}
.cube-scroll-list-wrapper {
  overflow: hidden;
}
.cube-pulldown-wrapper {
  position: absolute;
  width: 100%;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all;
}
.cube-pulldown-wrapper .before-trigger {
  height: 54px;
  line-height: 0;
  padding-top: 6px;
}
.cube-pulldown-wrapper .after-trigger .loading {
  padding: 8px 0;
}
.cube-pulldown-wrapper .after-trigger .cube-pulldown-loaded {
  padding: 12px 0;
}
.cube-pullup-wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.cube-pullup-wrapper .before-trigger {
  padding: 22px 0;
  min-height: 1em;
}
.cube-pullup-wrapper .after-trigger {
  padding: 19px 0;
}
.cube-scroll-content {
  position: relative;
  z-index: 1;
}
.cube-scroll-item {
  height: 60px;
  line-height: 60px;
  font-size: 18px;
  padding-left: 20px;
}
